const app = getApp();
export default {
	data() {
		return {
			form:{
				companyName:'',
				tfn:'',
				companyAddr:'',
				tel:'',
				bankOfDeposit:'',
				bankAccount:'',
				_id:''
			},
			isEdit:false
		};
	},
	onLoad(option){
		if(option.item !== undefined){
			this.form = JSON.parse(decodeURIComponent(option.item));
			this.isEdit = true
		}
	},
	methods: {
		handleDel:function(){
			const _self = this
			uni.showModal({
				title:'提示',
				content:'确认删除该发票信息？',
				success:function(result){
					if(result.confirm){
						_self.deleteInvoice()
					}
				}
			})
		},
		deleteInvoice(){
			app.globalData.wxDB.collection('InvoiceManage').doc(this.form._id).remove({
				success:function(res){
					if(res.errMsg === 'document.remove:ok'){
						uni.navigateBack({
							delta: 1
						})
					}
				}
			})
		},
		formSubmit: function(e) {
			const data = e.detail.value,
				tfnRule = /^[A-Z0-9]{15}$|^[A-Z0-9]{17}$|^[A-Z0-9]{18}$|^[A-Z0-9]{20}$/;
			if (data.companyName == '') {
				uni.showToast({
					title: '请输入公司名称',
					icon: 'none',
					position: 'bottom'
				});
				return false;
			} else if (data.tfn != '' && !tfnRule.test(data.tfn)) {
				uni.showToast({
					title: '请输入正确的税号',
					icon: 'none',
					position: 'bottom'
				});
				return false;
			}
			if(this.isEdit){
				app.globalData.wxDB.collection('InvoiceManage')
					.doc(this.form._id)
					.update({
						data:{
							companyName:data.companyName,
							bankAccount:data.bankAccount,
							bankOfDeposit:data.bankOfDeposit,
							companyAddr:data.companyAddr,
							tel:data.tel,
							tfn:data.tfn
						}
					}).then(result=>{
						uni.navigateBack({
							delta: 1
						})
					}).catch(err=>{
						uni.showModal({
							title:'错误提示',
							content:'编辑失败！程序异常'
						})
						console.error(err)
					})
			}else{
				app.globalData.wxDB
					.collection('InvoiceManage')
					.add({
						data: data
					})
					.then(res => {
						uni.navigateBack({
							delta: 1
						})
					})
					.catch(console.error);
			}
		}
	}
};
